import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Toast } from "vant";
import { Action } from "vuex-class";

import { Cell, CellGroup, ImagePreview } from "vant";
import Wintabe from "../../components/Wintabe.vue";
import Winbeet from "../../components/Winbeet.vue";
@Component({
  components: {
    Wintabe,
    Winbeet
  },
  mixins: [mixin]
})
export default class ProductDetail extends Vue {
  mounted() {
    console.log('进入该页面',sessionStorage.catId)
    this.catId = sessionStorage.catId,
    this.parentId = sessionStorage.parentId

    this.getproductList();
    this.getSecCatList();
  }

  parentId = ''
  changeRed= "0"
  getSecCatList(){
    //二级菜单
    Vue.prototype.$reqFormPost1(
      "/user/cat/list",
      {
        parentId: this.parentId
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log(res.message);
          return;
        }
        this["$Message"].success("成功");
        this.catList = res.data;
        console.log(res.data);
      }
    );
  }
  // 搜索列表
  seachList(index){
    this.changeRed = index;
    Vue.prototype.$reqFormPost1(
      "/user/goods/list",
      {
        catId: this.catId,
        sortName: '',
        sortStatus: ''
      },
      console.log('点击后的id', this.catId),
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log(res.message);
          return;
        }
        this["$Message"].success("成功");
        this.catList = res.data;
        console.log(res.data);
      }
    );
  }
  catId = '';
  catList = []
  sortName = ''
  sortStatus = ''
  getproductList(){
    console.log('开始取商品列表')
    Vue.prototype.$reqFormPost1(
      "/user/goods/list",
      {
        catId: this.catId
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log(res.message);
          return;
        }
        this["$Message"].success("成功");
        this.shopList = res.data.goodsList;
        console.log(res.data.goodsList);
      }
    );
  }
  shopList = [];
  goodsId = "";
  detatil:any= {
    commentList: [],
    detail: {
      imageList: []
    },
    goodsImg: "",
    goodsName: "",
    jingle: "",
    labelPrice: 0,
    marketPrice: 0,
    onlineStatus: "",
    sku: [],
    skuKey: [],
    storageNum: 0,
  };
  num = 1;
  skuattr = [];
  chosenList = [];
  chosensku = [];
  skuItem = {};
  commentnum = 0;
  praise = "0";
  tabindex = 0;
  selecttablist(index) {
    this.tabgoodslist = [];
    if (index == 0) {
      this.tabgoodslist = this.likeList;
      this.tabindex = 0;
    }
    if (index == 1) {
      this.tabgoodslist = this.newList;
      this.tabindex = 1;
    }
  }
 

  tabgoodslist = [];
  likeList = [];
  newList = [];
}



